import * as Main from '../../Js/main'
export default {
	name: 'Feed',

    components: {MainLayout,ContentWrapper,SwiperRow,ShareBoxFeed,PostItem,},
	methods: {
	
		getFormations: function() { 		
	let config = {	

		populate: {	

			image: "*",

	    instructor: {

	      populate: ['image'],

	    }

	  }

	}

	

	Request.getLocalTableArgs('formations', config)

	.then((res) => {

		this.formations = res.data.data

	})

}
,
		getWebinars: function() { 		
	let config = {	

		populate: {	

			image: "*",

	    instructor: {

	      populate: ['image'],

	    }

	  }

	}

	

	Request.getLocalTableArgs('webinars', config)

	.then((res) => {

		this.webinars = res.data.data

	})

}
,
		getArticle: function() { 		
	let config = {	

		populate: {	

			image: "*",

	    redactor: {

	      populate: ['image'],

	    }

	  }

	}

	

	Request.getLocalTableArgs('articles', config)

	.then((res) => {

		this.articles = res.data.data

	})

}
,
		getPosts: function() { 		
	let config = {	

		populate: {	

			image: "*",

			video: "*",

	    author: {

	      populate: ['image'],

	    }

	  }

	}

	

	Request.getTableArgs('posts', config)

	.then((res) => {

		this.posts = res.data.data

	})

}
,
	},
	mounted(){
			
	this.getFormations()

	this.getWebinars()

	this.getArticle()

	this.getPosts()

	},
	computed: {
	
		primaryColor: function() { 		
	return this.$store.state.primaryColor

}
,
		tabsName: function() { 		
	return [

		this.$t('Feed.formations'),

		this.$t('Feed.webinars'),

		this.$t('Feed.articles')

	]

}
,
	},
	data(){
